import { Component, ViewChild } from '@angular/core';
import { ChildViewChildComponent } from './child.viewchild.component';

@Component({
    selector: 'app-viewchild-parent',
    template: `
        <button (click)="addChild();">Click</button>
        <app-viewchild-child></app-viewchild-child>
    `
})


export class ParentViewChildComponent {
    @ViewChild(ChildViewChildComponent)
    myChild: ChildViewChildComponent;

    addChild() {
        this.myChild.value1++;
    };
}


