import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {

  formSignUp: FormGroup;
  constructor(private fb: FormBuilder) { 
    // this.formSignUp = new FormGroup({
    //   email: new FormControl('shdhfuhd@gmail.com'),
    //   password: new FormControl(),
    //   subject: new FormGroup({
    //     nodeJS: new FormControl(false),
    //     angular: new FormControl(true),
    //     reactJS: new FormControl(true)
    //   })
      
    // });


   
  }

  ngOnInit() {
    this.formSignUp = this.fb.group({
      email: ['', [Validators.email, gmailValidator]],
      password: '',
      subject: this.fb.group({
        nodeJS: false,
        angular: true,
        reactJS: true
      })
      
    });
  }

  onSubmit() {
    console.log(this.formSignUp.value);
  }

}


function gmailValidator(fc: FormControl) {
  if (fc.value.includes('@gmail.com')) {
    return null;
  }
  return { gmail: true };
}