import { Component, OnInit } from '@angular/core';
import { identifierModuleUrl } from '@angular/compiler';

@Component({
  selector: 'app-words',
  templateUrl: './words.component.html',
  styleUrls: ['./words.component.css']
})
export class WordsComponent implements OnInit {

  state: string = 'ALL';
  isShowForm = false;
  newEn: string = '';
  newVn: string = '';
  arrWords: any = [
    { id: 1, en: 'action', vn: 'hang dong', memorized: true },
    { id: 2, en: 'actor', vn: 'dien vien', memorized: false },
    { id: 3, en: 'active', vn: 'kich hoat', memorized: true }
  ];

  constructor() { }

  ngOnInit() {
  }

  addWord() {
    this.arrWords.unshift({
      id: this.arrWords.length + 1,
      en: this.newEn,
      vn: this.newVn,
      memorized: false
    });

    this.newVn = '';
    this.newEn = '';
    this.isShowForm = false;
  };

  showForm() {
    this.isShowForm = true;
  };


  removeWord(id) {
    let index = this.arrWords.findIndex(w => w.id === id);
    this.arrWords.splice(index, 1); 
  };


  changeStatus() {
    let ok = null;
    if (this.state == "RE")
      ok = true;
    else if (this.state == "NOT")
      ok = false;
    
    let newArrWord = this.arrWords.filter(w => w.memorized == ok || ok == null);
    console.log(newArrWord);
  };
}
